import React, { useRef, useState, memo } from 'react';
import { LoadingOutlined } from '@ant-design/icons';
import { message } from 'antd';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUpload } from '@fortawesome/free-solid-svg-icons';
import LazyImage from '../LazyImage';

const getBase64 = (img, callback) => {
    const reader = new FileReader();
    reader.readAsDataURL(img);
    reader.addEventListener('load', () => callback(reader.result));
};
const beforeUpload = (event) => {
    const file = event.target.files[0];
    if (!file) return false;
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    if (!isJpgOrPng) {
        message.error('Bạn phải tải tệp ảnh JPG/PNG !');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
        message.error('Ảnh của bạn phải nhỏ hơn 2MB!');
    }
    return isJpgOrPng && isLt2M;
};
const UploadComponent = ({ imageUrl, setImageUrl, ...props }) => {
    const inputRef = useRef();
    const [loading, setLoading] = useState(false);
    const handleChange = (event) => {
        if (!beforeUpload(event)) return;
        setLoading(true);
        getBase64(event.target.files[0], (base64) => {
            setLoading(false);
            setImageUrl(base64); // tra base64 từ event load của reader
        });
    };
    const handleClickOpenInputFile = () => {
        inputRef.current.click();
    };
    const UploadButton = () => (
        <button className="w-full" type="button" onClick={handleClickOpenInputFile}>
            {loading ? <LoadingOutlined /> : <FontAwesomeIcon icon={faUpload} />}
            <div
                style={{
                    marginTop: 8,
                }}
            >
                <span className="font-semibold">Tải liên</span>
            </div>
        </button>
    );
    return (
        <div {...props}>
            <input type="file" className="hidden" ref={inputRef} onChange={handleChange} />
            <div
                className={`border-2 border-dashed h-44 w-full ${
                    !imageUrl?.length > 0 ? 'py-5' : ''
                } border-primary flex rounded-md justify-center content-center`}
            >
                {imageUrl ? (
                    <button className="w-full h-full" onClick={() => handleClickOpenInputFile()}>
                        {/* <img
                            src={imageUrl}
                            className="rounded-md cursor-pointer w-full object-cover h-full"
                            alt="thumbnail"
                        /> */}
                        <LazyImage src={imageUrl} alt="thumbnail" className="w-full h-full" />
                    </button>
                ) : (
                    <UploadButton />
                )}
            </div>
        </div>
    );
};
export default memo(UploadComponent);
